import "./App.css";
import QrGenerator from "./components/QrGenerator";
import QrScanner from "./components/QrScanner";
import GuestsList from "./components/GuestsList";
import Header from "./components/Header";
import Login from "./components/Login";

import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import { useState, useEffect } from "react";

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [role, setRole] = useState(null);

  useEffect(() => {
    const token = localStorage.getItem("authToken");
    const storedRole = localStorage.getItem("authRole");
    if (token) {
      setIsAuthenticated(true);
      setRole(storedRole);
    }
  }, []);

  const handleLogin = (username, password) => {
    if (username === "admin" && password === "admin") {
      localStorage.setItem("authToken", "logged-in");
      localStorage.setItem("authRole", "admin");
      setRole("admin");
      setIsAuthenticated(true);
      return true;
    }

    if (username === "user" && password === "user") {
      localStorage.setItem("authToken", "logged-in");
      localStorage.setItem("authRole", "user");
      setRole("user");
      setIsAuthenticated(true);
      return true;
    }

    return false;
  };

  const handleLogout = () => {
    localStorage.removeItem("authToken");
    localStorage.removeItem("authRole");
    setIsAuthenticated(false);
    setRole(null);
  };

  return (
    <div
      style={{
        backgroundImage: `url("/bg-image.jpg")`,
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        minHeight: "100vh",
      }}
    >
      <Router>
        <Header onLogout={handleLogout} isAuthenticated={isAuthenticated} role={role} />
        <Routes>
          {/* Public Route */}
          <Route path="/" element={<QrGenerator />} />

          {/* Login Page */}
          <Route path="/login" element={<Login onLogin={handleLogin} isAuthenticated={isAuthenticated} />} />

          {/* Protected Admin Routes */}
          <Route
            path="/admin/*"
            element={
              isAuthenticated ? (
                <Routes>
                  <Route path="scanner" element={role === "admin" ? <QrScanner /> : <Navigate to="/admin" replace />} />
                  <Route path="guests" element={role === "admin" ? <GuestsList /> : <Navigate to="/admin" replace />} />
                  <Route path="" element={<div className="text-center text-xl p-6">Welcome to Admin Panel</div>} />
                </Routes>
              ) : (
                <Navigate to="/login" replace />
              )
            }
          />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
