import React, { useState } from "react";
import loadScan from "../Images/loadScan.png";
import { CircularProgress } from "@chakra-ui/react";

let prevVal;

function QrGenerator() {
  const [src, setSrc] = useState("");
  const [loading, setLoading] = useState(false);
  const [showQrPanel, setShowQrPanel] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Form fields
  const [fullName, setFullName] = useState("");
  const [cnic, setCnic] = useState(""); //  NEW FIELD
  const [email, setEmail] = useState("");
  const [contact, setContact] = useState("");
  const [profession, setProfession] = useState("");
  const [organization, setOrganization] = useState("");
  const [designation, setDesignation] = useState("");
  const [country, setCountry] = useState("");
  const [picture, setPicture] = useState(null);

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onloadend = () => {
      setPicture(reader.result);
    };
    reader.readAsDataURL(file);
  };

  const genQr = async () => {
    setErrorMsg("");

    if (
      !fullName ||
      !cnic ||
      !email ||
      !contact ||
      !profession ||
      !organization ||
      !designation ||
      !country
    ) {
      setErrorMsg(" All fields are required.");
      return;
    }

    try {
      //  FETCH existing registrations
      const existingRes = await fetch(
        "https://stackapk.com/qr-server/api/registrations"
      );
      const existingData = await existingRes.json();

      const registrationsArray = Array.isArray(existingData)
        ? existingData
        : existingData.registrations || [];

      //  Normalize CNIC
      const normalizedInputCnic = cnic.replace(/\D/g, ""); // digits only
      const duplicate = registrationsArray.find((entry) => {
        const storedCnic = (entry.cnic || "").replace(/\D/g, "");
        return storedCnic && storedCnic === normalizedInputCnic;
      });

      if (duplicate) {
        setErrorMsg(` CNIC ${cnic} is already registered.`);
        return;
      }
    } catch (err) {
      console.warn("Could not check for duplicates:", err);
      // Continue anyway (fail open)
    }

    //  Prepare data for backend
    const dataObj = {
      fullName,
      cnic,
      email,
      contact,
      profession,
      organization,
      designation,
      country,
    };
    if (picture) dataObj.picture = picture;

    //  Include CNIC in QR Code data
    const qrDataObj = {
      fullName,
      cnic,
      email,
      contact,
      profession,
      organization,
      designation,
      country,
    };

    const combinedValue = Object.entries(qrDataObj)
      .map(([key, value]) => `${key}: ${value}`)
      .join("\n");

    if (prevVal === combinedValue) return;

    setIsSubmitting(true);

    try {
      const response = await fetch(
        "https://stackapk.com/qr-server/api/register",
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(dataObj),
        }
      );

      const result = await response.json();
      if (!response.ok) {
        setErrorMsg(result.error || "Something went wrong.");
        setIsSubmitting(false);
        return;
      }

      prevVal = combinedValue;
      setShowQrPanel(true);
      setSrc(
        `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${encodeURIComponent(
          combinedValue
        )}`
      );
      setLoading(true);
    } catch (err) {
      console.error(err);
      setErrorMsg("Network error. Please try again.");
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleImageLoad = () => setLoading(false);

  return (
    <div
      className="pt-20 flex justify-center items-center min-h-screen  px-4 sm:px-8"
      style={{ height: "100vh" }}
    >
      <div className="text-black flex flex-col lg:flex-row gap-6 w-full max-w-6xl items-center">
        {/* Left Side - Form */}
        <div className="bg-white w-full lg:w-[35rem] space-y-4 px-4 sm:px-8 shadow-xl rounded-2xl py-6">
          <h1 className="font-extrabold text-xl sm:text-2xl text-center text-[rgb(27,41,75)]">
            QR Code Registration
          </h1>

          {errorMsg && (
            <div className="bg-red-100 text-red-700 px-4 py-2 rounded-md text-sm">
              {errorMsg}
            </div>
          )}

          {[
            { value: fullName, setter: setFullName, placeholder: "Full Name" },
            { value: cnic, setter: setCnic, placeholder: "CNIC (e.g. 12345-6789012-3)" },         
          ].map((field, i) => (
            <input
              key={i}
              type={field.type || "text"}
              value={field.value}
              onChange={(e) => field.setter(e.target.value)}
              placeholder={field.placeholder}
              className="w-full px-3 sm:px-4 py-2 border-2 rounded-md outline-none text-base sm:text-lg"
            />
          ))}


         <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
  {[
    { value: email, setter: setEmail, placeholder: "Email", type: "email" },        
    { value: contact, setter: setContact, placeholder: "Contact Number" },
    { value: profession, setter: setProfession, placeholder: "Profession" },
    { value: organization, setter: setOrganization, placeholder: "Organization" }
  ].map((field, i) => (
    <input
      key={i}
      type={field.type || "text"}
      value={field.value}
      onChange={(e) => field.setter(e.target.value)}
      placeholder={field.placeholder}
      className="w-full px-3 sm:px-4 py-2 border-2 rounded-md outline-none text-base sm:text-lg"
    />
  ))}
</div>

 <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
   <input
      type={"text"}
      value={designation}
      onChange={(e) => setDesignation(e.target.value)}
      placeholder={"Designation"}
      className="w-full px-3 sm:px-4 py-2 border-2 rounded-md outline-none text-base sm:text-lg"
    />

 {/*  Country Dropdown */}
          <select
            value={country}
            onChange={(e) => setCountry(e.target.value)}
            className="w-full px-3 sm:px-4 py-2 border-2 rounded-md outline-none text-base sm:text-lg"
          >
            <option value="">Select Country</option>
            {[
              "Afghanistan", "Albania", "Algeria", "Andorra", "Angola", "Argentina", "Australia", "Austria", "Bangladesh",
              "Belgium", "Bhutan", "Brazil", "Canada", "China", "Colombia", "Denmark", "Egypt", "Finland", "France", "Germany",
              "Greece", "Hong Kong", "Hungary", "Iceland", "India", "Indonesia", "Iran", "Iraq", "Ireland", "Israel", "Italy",
              "Japan", "Jordan", "Kazakhstan", "Kenya", "Kuwait", "Latvia", "Lebanon", "Lithuania", "Luxembourg", "Malaysia",
              "Maldives", "Mexico", "Monaco", "Mongolia", "Morocco", "Nepal", "Netherlands", "New Zealand", "Nigeria", "Norway",
              "Oman", "Pakistan", "Philippines", "Poland", "Portugal", "Qatar", "Romania", "Russia", "Saudi Arabia", "Singapore",
              "South Africa", "South Korea", "Spain", "Sri Lanka", "Sweden", "Switzerland", "Syria", "Thailand", "Turkey",
              "Ukraine", "United Arab Emirates", "United Kingdom", "United States", "Vietnam", "Yemen", "Zimbabwe"
            ].map((c) => (
              <option key={c} value={c}>
                {c}
              </option>
            ))}
          </select>
 </div>

         
          {/* Picture Upload (Optional) */}
          <div>
            <label className="block text-gray-700 mb-2 font-medium">
              Upload Picture (Optional)
            </label>
            <input
              type="file"
              accept="image/*"
              onChange={handleFileChange}
              className="w-full"
            />
            {picture && (
              <img
                src={picture}
                alt="Preview"
                className="mt-3 w-24 h-24 object-cover rounded-md border"
              />
            )}
          </div>

          <button
            onClick={genQr}
            disabled={isSubmitting}
            className={`w-full text-white text-lg h-12 cursor-pointer font-bold flex justify-center items-center rounded-md ${isSubmitting
                ? "bg-gray-400"
                : "bg-[rgb(3,169,244)] hover:bg-blue-500"
              } transition`}
          >
            {isSubmitting ? "Registering..." : "Register"}
          </button>
        </div>

        {/* Right Side - QR Code Panel */}
        {showQrPanel && (
          <div className="space-y-4 px-4 sm:px-6 relative rounded-2xl w-full max-w-sm mx-auto lg:mx-0 py-6 shadow-xl bg-white">
            <div className="bg-yellow-100 border-l-4 border-yellow-500 text-yellow-900 p-3 rounded-md text-sm sm:text-base font-medium">
              <p>
                <strong> Please save this QR code</strong> as it serves as your
                official <strong>Entry Pass</strong>.
              </p>
            </div>

            <div className="w-full h-64 sm:h-80 flex justify-center items-center border border-gray-300 rounded-md">
              {src ? (
                <img
                  alt="QR"
                  onLoad={handleImageLoad}
                  className="w-32 sm:w-40"
                  src={src}
                />
              ) : (
                <img alt="" src={loadScan} className="w-32 sm:w-40" />
              )}
            </div>

            {loading && (
              <div className="absolute top-28 sm:top-36 left-1/2 -translate-x-1/2">
                <CircularProgress isIndeterminate color="green.300" />
              </div>
            )}

            <div className="text-white font-bold space-x-3 h-12 flex cursor-pointer justify-center items-center rounded-md bg-[rgb(144,197,45)] hover:bg-green-600 transition">
              <i className="fa-sharp fa-solid text-xl sm:text-2xl fa-circle-down"></i>
              <div className="text-center">
                <a target="_blank" rel="noreferrer" href={src} download="QR">
                  <p>Download</p>
                  <p className="text-xs text-[rgb(240,243,235)]">PNG</p>
                </a>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default QrGenerator;
