import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

const Login = ({ onLogin, isAuthenticated }) => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    if (isAuthenticated) navigate("/"); // redirect if already logged in
  }, [isAuthenticated, navigate]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const success = onLogin(username, password);
    if (success) {
      navigate("/"); // redirect to homepage after login
    } else {
      setError("Invalid username or password");
    }
  };

  return (
    <div
      className="flex justify-center items-center min-h-screen bg-[rgb(219,226,255)] overflow-hidden"
      style={{ height: "100vh" }} //  ensures no scroll bar
    >
      <form
        onSubmit={handleSubmit}
        className="bg-white p-6 rounded-2xl shadow-xl w-full max-w-md space-y-4"
      >
        <h2 className="text-center text-2xl font-bold text-[rgb(27,41,75)]">
          Login
        </h2>

        {error && (
          <div className="bg-red-100 text-red-700 px-4 py-2 rounded-md text-sm">
            {error}
          </div>
        )}

        <input
          type="text"
          placeholder="Username"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          className="w-full px-3 py-2 border-2 rounded-md outline-none text-lg"
        />
        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className="w-full px-3 py-2 border-2 rounded-md outline-none text-lg"
        />
        <button
          type="submit"
          className="w-full text-white text-lg h-12 cursor-pointer font-bold flex justify-center items-center rounded-md bg-[rgb(3,169,244)] hover:bg-blue-500 transition"
        >
          Login
        </button>
      </form>
    </div>
  );
};

export default Login;
