import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import logo from "../Images/logo.png";

function Header({ onLogout, isAuthenticated, role }) {
  const navigate = useNavigate();
  const [menuOpen, setMenuOpen] = useState(false);

  const handleLogout = () => {
    if (onLogout) {
      onLogout();
      navigate("/login");
    }
    setMenuOpen(false);
  };

  return (
    <header className="fixed top-0 left-0 w-full bg-[rgb(27,41,75)] text-white shadow-md z-50">
      <div className="max-w-6xl mx-auto flex justify-between items-center px-4 py-3">
        {/* Logo */}
        <Link to="/" className="flex items-center gap-3" onClick={() => setMenuOpen(false)}>
          <img src={logo} alt="Logo" className="h-10 w-10 object-contain" />
          <span className="font-bold text-lg sm:text-xl">QR Registration</span>
        </Link>

        {/* Desktop Navigation */}
        <nav className="hidden md:flex items-center gap-4">
          {/* Always show Register link */}
          <Link
            to="/"
            className="hover:text-blue-300 transition text-sm sm:text-base"
          >
            Register
          </Link>

          {/* Show admin links only if authenticated */}
          {isAuthenticated && role === "admin" && (
            <>
              <Link
                to="/admin/scanner"
                className="hover:text-blue-300 transition text-sm sm:text-base"
              >
                Scanner
              </Link>
              <Link
                to="/admin/guests"
                className="hover:text-blue-300 transition text-sm sm:text-base"
              >
                Guests
              </Link>
            </>
          )}

          {/* Logout button only if logged in */}
          {isAuthenticated && (
            <button
              onClick={handleLogout}
              className="bg-red-500 px-3 py-1 rounded-md text-sm sm:text-base hover:bg-red-600 transition"
            >
              Logout
            </button>
          )}
        </nav>

        {/* Mobile Menu Button */}
        <button
          onClick={() => setMenuOpen(!menuOpen)}
          className="md:hidden p-2 rounded hover:bg-white/10 transition text-2xl"
        >
          {menuOpen ? "" : ""}
        </button>
      </div>

      {/* Mobile Navigation Dropdown */}
      {menuOpen && (
        <div className="md:hidden bg-[rgb(27,41,75)] border-t border-white/20 flex flex-col gap-4 px-4 py-4">
          <Link
            to="/"
            className="hover:text-blue-300 transition"
            onClick={() => setMenuOpen(false)}
          >
            Register
          </Link>

          {isAuthenticated && role === "admin" && (
            <>
              <Link
                to="/admin/scanner"
                className="hover:text-blue-300 transition"
                onClick={() => setMenuOpen(false)}
              >
                Scanner
              </Link>
              <Link
                to="/admin/guests"
                className="hover:text-blue-300 transition"
                onClick={() => setMenuOpen(false)}
              >
                Guests
              </Link>
            </>
          )}

          {isAuthenticated && (
            <button
              onClick={handleLogout}
              className="bg-red-500 px-3 py-2 rounded-md hover:bg-red-600 transition"
            >
              Logout
            </button>
          )}
        </div>
      )}
    </header>
  );
}

export default Header;
