import React, { useState, useEffect } from "react";
import { QrReader } from "react-qr-reader";

const QrScanner = () => {
  const [rawScanned, setRawScanned] = useState("");
  const [parsedScanned, setParsedScanned] = useState(null);
  const [validEntries, setValidEntries] = useState([]);
  const [matchedEntry, setMatchedEntry] = useState(null);
  const [isVerified, setIsVerified] = useState(null);
  const [error, setError] = useState("");

  useEffect(() => {
    fetch("https://stackapk.com/qr-server/api/registrations")
      .then((res) => {
        if (!res.ok) throw new Error(`HTTP error! status: ${res.status}`);
        return res.json();
      })
      .then((data) => {
        if (Array.isArray(data)) setValidEntries(data);
        else setError("Invalid data format from backend");
      })
      .catch((err) => {
        console.error(err);
        setError("Failed to load registration data.");
      });
  }, []);

  const parseScannedText = (text) => {
    const obj = {};
    text.split("\n").forEach((line) => {
      const [key, ...rest] = line.split(":");
      if (key && rest.length > 0) {
        obj[key.trim().toLowerCase()] = rest.join(":").trim();
      }
    });
    return obj;
  };

  useEffect(() => {
    if (!rawScanned) {
      setParsedScanned(null);
      setIsVerified(null);
      setMatchedEntry(null);
      return;
    }

    const parsed = parseScannedText(rawScanned);
    setParsedScanned(parsed);

    const cnic = (parsed.cnic || "").toLowerCase().trim();
    const found = validEntries.find(
      (entry) => (entry.cnic || "").toLowerCase().trim() === cnic
    );

    if (found) {
      setIsVerified(true);
      setMatchedEntry(found);
    } else {
      setIsVerified(false);
      setMatchedEntry(null);
    }

    setError("");
  }, [rawScanned, validEntries]);

  const handleScan = (data) => {
    if (data && data !== rawScanned) {
      setRawScanned(data);
    }
  };

  const handleError = (err) => {
    console.error(err);
    setError("QR scanning error.");
  };

  const resetScanner = () => {
    setRawScanned("");
    setParsedScanned(null);
    setIsVerified(null);
    setMatchedEntry(null);
    setError("");
  };

  return (
    <div style={styles.page}>
      <div style={styles.card}>
        <h2 style={styles.title}>QR Scanner</h2>

        <div style={styles.reader}>
          <QrReader
            delay={300}
            onError={handleError}
            onResult={(result, error) => {
              if (result?.text) handleScan(result.text);
              if (error) console.warn(error);
            }}
            constraints={{ facingMode: "environment" }}
            style={{ width: "100%" }}
          />
        </div>

        {rawScanned && (
          <div style={styles.resultBox}>
            <p
              style={{
                color: isVerified ? "#1a7f37" : "#d32f2f",
                fontWeight: "bold",
                fontSize: "1.1rem",
                textAlign: "center",
                marginBottom: 10,
              }}
            >
              {isVerified === null
                ? ""
                : isVerified
                ? " Record Found"
                : " Record NOT Found"}
            </p>

            {parsedScanned && (
              <div style={styles.section}>
                <p style={styles.sectionTitle}>Scanned Details</p>
                <div style={styles.parsedBox}>
                  {Object.entries(parsedScanned).map(([key, value]) => (
                    <div key={key} style={styles.row}>
                      <strong>{key}:</strong> <span>{value}</span>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {matchedEntry && (
              <div style={styles.imageContainer}>
                <p style={{ fontWeight: "bold", marginBottom: 6 }}>
                  Uploaded Picture
                </p>
                {matchedEntry.picture ? (
                  <img
                    src={
                      matchedEntry.picture.startsWith("http")
                        ? matchedEntry.picture
                        : `https://stackapk.com/qr-server/${matchedEntry.picture.replace(
                            "../",
                            ""
                          )}`
                    }
                    alt="User"
                    style={styles.image}
                  />
                ) : (
                  <p style={{ color: "gray" }}>No picture uploaded</p>
                )}
              </div>
            )}

            <button style={styles.button} onClick={resetScanner}>
              Scan Next QR
            </button>
          </div>
        )}

        {error && <p style={styles.error}>{error}</p>}
      </div>
    </div>
  );
};

const styles = {
  page: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    minHeight: "100vh",
   
    padding: "1rem",
  },
  card: {
    width: "100%",
    maxWidth: "450px",
    background: "#fff",
    borderRadius: "16px",
    boxShadow: "0 8px 20px rgba(0,0,0,0.08)",
    padding: "1.5rem",
    textAlign: "center",
    display: "flex",
    flexDirection: "column",
    gap: "1rem",
  },
  title: {
    fontSize: "1.6rem",
    fontWeight: "bold",
    color: "#1b294b",
  },
  reader: {
    width: "100%",
    borderRadius: 12,
    overflow: "hidden",
  },
  resultBox: {
    background: "#f9fafb",
    padding: "1rem",
    borderRadius: "12px",
    textAlign: "center",
  },
  section: {
    marginTop: "0.5rem",
    padding: "0.5rem",
    background: "#fff",
    borderRadius: "8px",
    boxShadow: "0 2px 6px rgba(0,0,0,0.05)",
  },
  sectionTitle: {
    fontWeight: "bold",
    fontSize: "1rem",
    marginBottom: "0.5rem",
    textAlign: "center",
  },
  parsedBox: {
    display: "flex",
    flexDirection: "column",
    gap: "6px",
    fontSize: "0.9rem",
  },
  row: {
    display: "flex",
    justifyContent: "space-between",
  },
  imageContainer: {
    marginTop: "1rem",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  image: {
    maxWidth: "200px",
    maxHeight: "200px",
    borderRadius: 8,
    border: "2px solid #e0e0e0",
  },
  button: {
    marginTop: "1rem",
    background: "#1b294b",
    color: "#fff",
    border: "none",
    padding: "0.6rem 1.2rem",
    borderRadius: "8px",
    cursor: "pointer",
    fontSize: "1rem",
    transition: "background 0.3s ease",
  },
  error: {
    color: "#d32f2f",
    fontSize: "0.9rem",
    marginTop: "0.5rem",
  },
};

export default QrScanner;
